<template>
    <div class="m-wiki-wrapper">
        <Search />
        <SourceSingle
            v-if="wiki_post && wiki_post.source"
            :source="wiki_post.source"
            :fav-enable="true"
        />
        <div class="m-wiki" v-if="wiki_post && wiki_post.post">
            <WikiPanel class="m-quest-panel" :wiki-post="wiki_post">
                <template slot="head-title">
                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512" v-bind:class="'u-icon'" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path d="M507.513 94.223L417.779 4.488A15.269 15.269 0 00406.618.004a15.315 15.315 0 00-10.958 4.957L271.069 140.675c-3.273-25.551-25.141-45.367-51.568-45.367-28.677 0-52.006 23.33-52.006 52.006 0 7.732 3.334 17.808 8.116 28.297-10.489-4.782-20.565-8.116-28.297-8.116-28.676 0-52.006 23.329-52.006 52.006 0 26.425 19.817 48.295 45.367 51.568L4.961 395.659a15.323 15.323 0 00-.473 22.119l89.734 89.734a15.321 15.321 0 0022.119-.472l187.001-203.697 203.697-187.001a15.324 15.324 0 00.474-22.119zM219.502 125.95c11.781 0 21.364 9.584 21.364 21.364 0 .864-.452 9.206-16.112 35.728a397.246 397.246 0 01-5.252 8.637 401.184 401.184 0 01-5.253-8.637c-15.66-26.522-16.112-34.864-16.112-35.728 0-11.781 9.584-21.364 21.365-21.364zm-93.553 93.551c0-11.781 9.584-21.364 21.364-21.364.864 0 9.206.452 35.728 16.112a398.71 398.71 0 018.663 5.269c-18.638 11.574-37.679 21.348-44.392 21.348-11.778 0-21.363-9.584-21.363-21.365zM104.584 474.54l-67.121-67.121L219.05 240.716l52.238 52.238L104.584 474.54zm188.369-203.254l-52.238-52.238L407.418 37.462l67.121 67.122-181.586 166.702z"/></svg>
                    <span>任务攻略</span>
                </template>
                <template slot="head-actions">
                    <a
                        class="el-button el-button--primary"
                        :href="publish_url(`quest/${id}`)"
                    >
                        <i class="el-icon-edit"></i>
                        <span>完善任务百科</span>
                    </a>
                </template>
                <template slot="body">
                    <Article :content="wiki_post.post.content" />
                    <Thx
                        class="m-thx"
                        slot="single-append"
                        :postId="id"
                        postType="quest"
                        :userId="author_id"
                        :adminBoxcoinEnable="isRevision"
                        :userBoxcoinEnable="isRevision"
                    />
                </template>
            </WikiPanel>

            <WikiRevisions
                v-if="id"
                type="quest"
                :source-id="id"
                style="margin-bottom:35px"
            />

            <template v-if="id">
                <el-divider content-position="left">讨论</el-divider>
                <Comment :id="id" category="quest" />
            </template>
        </div>

        <div v-else class="m-wiki-null">
            <i class="el-icon-s-opportunity"></i>
            <span> 暂无内容，我要</span>
            <a class="s-link" :href="publish_url(`quest/${id}`)"
                >完善任务百科</a
            >
        </div>
    </div>
</template>

<script>
import { postStat } from "@jx3box/jx3box-common/js/stat";
import { WikiPost } from "@jx3box/jx3box-common/js/helper";
import { publishLink } from "@jx3box/jx3box-common/js/utils";
import SourceSingle from "../components/SourceSingle";
import WikiPanel from "@jx3box/jx3box-common-ui/src/wiki/WikiPanel";
import WikiRevisions from "@jx3box/jx3box-common-ui/src/wiki/WikiRevisions";
import Search from "../components/Search";
import Article from "@jx3box/jx3box-editor/src/Article.vue";
import Comment from "@jx3box/jx3box-comment-ui/src/Comment.vue";

export default {
    name: "Detail",
    props: [],
    data: function() {
        return {
            wiki_post: "",
        };
    },
    computed: {
        id: function() {
            return this.$route.params.source_id;
        },
        type: function() {
            return this.wiki_post.source.type;
        },
        isRevision: function () {
            return !!this.$route.params.post_id;
        },
        author_id: function () {
            return ~~this.wiki_post.post.user_id;
        },
    },
    methods: {
        publish_url: publishLink,
    },
    created: function() {
        if (this.id) {
            postStat("quest", this.id);
        }
    },
    components: {
        SourceSingle,
        Article,
        WikiPanel,
        WikiRevisions,
        Comment,
        Search,
    },
    watch: {
        id: {
            immediate: true,
            handler() {
                // 获取最新攻略
                if (this.id) {
                    WikiPost.newest("quest", this.id)
                        .then((res) => {
                            res = res.data;
                            if (res.code === 200) {
                                this.wiki_post = res.data;
                                if (this.wiki_post.source)
                                    this.wiki_post.source.post = this.wiki_post.post;
                            }
                        })
                        .then(() => {
                            // 提交子统计
                            postStat(this.type, this.id);
                        });
                }
            },
        },
        "$route.params.post_id": {
            immediate: true,
            handler() {
                if (this.$route.params.post_id) {
                    // 获取指定攻略
                    WikiPost.view(this.$route.params.post_id)
                        .then((res) => {
                            res = res.data;
                            if (res.code === 200) {
                                this.wiki_post = res.data;
                                if (this.wiki_post.source)
                                    this.wiki_post.source.post = this.wiki_post.post;
                            }
                        })
                        .then(() => {
                            // 提交子统计
                            postStat(this.type, this.id);
                        });
                }
            },
        },
    },
};
</script>

<style lang="less">
@import "../assets/css/views/detail.less";
</style>
